#jobs {
  padding-top: 90px;
  padding-bottom: 90px;
}

.jobs__header {
  margin-bottom: 50px;
  max-width: 860px;
  color: $secondaryColor;
}

.jobs__row {
  display: flex;
  justify-content: space-between;
}

.job-desc {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.35;

  color: $textColor;

  max-width: 475px;
}

.job-name {
  color: $thirdColor;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.04;
}

.job-item {
  padding: 50px;

  background: $primaryColor;
  border-radius: 48px;

  width: 46%;
}

.job-item > * + * {
  margin-top: 30px;
}

.jobs__list > * + * {
  margin-top: 60px;
}

.job-column {
  width: 46%;
  & .job-item {
    width: 100%;

    & .job-desc {
      margin-top: 0;
    }
  }
}

.job-column > * + * {
  margin-top: 60px;
}
