#mission {
  padding-top: 90px;
  padding-bottom: 90px;
  text-align: center;

  background-image: url(../images/mission/mission-bg.png);
  background-repeat: no-repeat;
  background-position: top center;
  //   background-attachment: fixed;

  background-color: #050315;
}

.mission__wrapper > * + * {
