#clients {
  padding-top: 90px;
  padding-bottom: 90px;

  font-size: 40px;
  font-weight: 400;
  font-family: normalFont, sans-serif;
  color: $textColor;
}

.clients__header {
  max-width: 915px;

  & .title-h2 {
    color: $secondaryColor;
  }
}

.clients__header > * + * {
  margin-top: 40px;
}

.clients__wrapper > * + * {
  margin-top: 180px;
}

.clients__top > * + *,
.clients__bottom > * + * {
  margin-top: 60px;
}

.clients__row {
  display: flex;
  justify-content: space-between;
}

.client-item {
  display: flex;
  align-items: center;
}

.client-item > * + * {
  margin-left: 30px;
}

.clients-left > * + *,
.clients-right > * + * {
  margin-top: 60px;
}

.client-name {
  font-family: headerFont, sans-serif;
  font-weight: 700;
  font-size: 45px;
}

.clients__more {
    margin-top: 90px;
  font-family: normalFont, sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 1;

  text-align: center;

  color: #b1b1c2;
}
