#work-value {
  padding-bottom: 90px;
}

.work-value__wrapper > * + * {
  margin-top: 60px;
}

.work-value-header {
  margin-bottom: 50px;
  & h3.title-h2 {
  }
}

.work-value-header > * + * {
  margin-top: 10px;
}

.work-value-item {
  padding: 85px;
  display: flex;
  justify-content: space-between;

  min-height: 900px;
  border-radius: 48px;
}

.work-value-text > * + * {
  margin-top: 30px;
}

.work-value-item.creators {
  background-color: #01549f;
  background-image: url(../images/work-value/creators-bg.png);
  background-position: top center;
  background-repeat: repeat-x;

  & .work-value-header {
    & h3 {
      color: #55acff;
    }
  }

  & .work-value-img {
    background-color: #01549f;
  }
}

.work-value-item.customers {
  background-color: #4629ba;
  background-image: url(../images/work-value/customers-bg.png);
  background-position: top center;
  background-repeat: repeat-x;

  & .work-value-header {
    & h3 {
      color: #e2a6ff;
    }
  }

  & .work-value-img {
    background-color: #8b81fd;
  }
}

.work-value-item.strategy {
  background-color: #cc9c24;
  background-image: url(../images/work-value/strategy-bg.png);
  background-position: top center;
  background-repeat: repeat-x;

  & .work-value-header {
    & h3 {
      color: #ffe713;
    }
  }

  & .work-value-img {
    background-color: #e4b540;
  }
}

.work-value-img {
  display: inline-block;
  padding: 70px;
  border-radius: 48px;
  background-color: aquamarine;
}

.work-value-text {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.35;

  max-width: 875px;
}
