#why-we {
  padding-top: 90px;
  padding-bottom: 90px;

  background-image: url(../images/why-we/why-we-bg.png);
  background-repeat: no-repeat;
  background-position: top center;
  //   background-attachment: fixed;

  background-color: #161108;
}

.why-we__wrapper {
  margin: 0 auto;
  max-width: 1400px;
}

.why-we__wrapper > * + * {
  margin-top: 50px;
}

.why-we__category {
  color: $secondaryColor;
  font-size: 30px;
  text-align: center;
}

.why-we__text {
  font-size: 45px;
  max-width: 800px;
  margin-top:20px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;
}
